:root {

	/* Z-index levels */
}

.root {
	position: relative;
	display: inline-block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 200px;
}

.root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.controls {
	position: relative;
}

.dropdown {
	border: 1px solid #424242;
	background: #fff;
	position: absolute;
	width: 100%;
	z-index: 200;
}

.clear,
.trigger {
	position: absolute;
	cursor: pointer;
	border: none;
	background: none;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	padding: 0;
}

.trigger {
	right: 7px;
}

.clear {
	right: 27px;
}

.focused { }

.valid { }

.invalid { }

.open { }

.clearable { }
